.title{
    font-size: 23px;
    font-weight: 700;
}

.daily-item{
    display:flex;
    background-color: rgb(162, 168, 167);
    opacity: 0.8;
    border-radius: 15px;
    height:40px;
    margin:5px;
    align-items:center;
    cursor: pointer;
    font-size:14px;
    padding:5px 15px
}

.icon-small{
    width:40px;
}

.day{
    color:#212121;
    flex:1 1;
    font-weight:800;
    margin-left: 15px;
}

.description{
    flex:1 1;
    margin-right:15px;
    text-align: right;
}

.min-max{
    color:#504e4e;
}

.daily-details-grid{
    display:grid;
    grid-row-gap:0;
    grid-column-gap:15px;
    row-gap:0;
    column-gap:15px;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding:5px 15px;
}

.daily-details-grid-item{
    display:flex;
    align-items:center;
    height:30px;
    justify-content: space-between;
}

.daily-details-grid-item label:first-child{
    color:#1f1d1d;
    font-weight:500;
}

.daily-details-grid-item label:last-child{
    color:#353434;
}